import { ShapeFlags } from "@vue/share";
import { isSameVnode } from "./createVnode";
export function createRenderer(renderOptions) {
  // core中不关心如何进行的渲染
  const {
    insert: hostInsert,
    remove: hostRemove,
    setElementText: hostSetElementText,
    setText: hostSetText,
    patchProp: hostPatchProp,
    createElement: hostCreateElement,
    createText: hostCreateText,
    parentNode: hostParentNode,
    nextSibling: hostNextSibling,
  } = renderOptions;

  const mountChildren = (children, container) => {
    for (let i = 0; i < children.length; i++) {
      //TODO: children[i] 可能是纯文本元素
      patch(null, children[i], container);
    }
  };
  const mountElement = (vnode, container) => {
    const { type, children, props, shapeFlag } = vnode;
    // 在第一次渲染时，将虚拟节点与真实dom关联起来
    let el = (vnode.el = hostCreateElement(type));
    if (props) {
      for (let key in props) {
        hostPatchProp(el, key, null, props[key]);
      }
    }

    if (shapeFlag & ShapeFlags.TEXT_CHILDREN) {
      // 如果子节点是文本
      hostSetElementText(el, children);
    } else if (shapeFlag & ShapeFlags.ARRAY_CHILDREN) {
      // 如果子节点是数组
      mountChildren(children, el);
    }

    hostInsert(el, container);
  };

  const processElement = (oldVode, newVnode, container) => {
    if (oldVode == null) {
      // 初始化操作
      mountElement(newVnode, container);
    } else {
      patchElement(oldVode, newVnode, container);
    }
  };
  /**
   * 比较两个元素差异，进行复用
   * 比较属性和元素子节点
   * @param oldVNode
   * @param newVNode
   * @param container
   */
  const patchElement = (oldVNode, newVNode, container) => {
    let el = (oldVNode.el = newVNode.el); // 比较差异，更新new的，然后复用给old
    let oldProps = oldVNode.props || {};
    let newProps = newVNode.props || {};
    console.log("jingguo");
  };
  // 渲染和更新都通过这个函数
  const patch = (oldVnode, newVnode, container) => {
    if (oldVnode == newVnode) {
      // 如果相同直接返回就行
      return;
    }
    // 需要进行diff比较，如果不是同一类型，直接替换
    if (oldVnode && !isSameVnode(oldVnode, newVnode)) {
      unmount(oldVnode);
      oldVnode = null; // 直接去除掉，去执行下面的逻辑
    }

    processElement(oldVnode, newVnode, container);
  };

  const unmount = (vnode) => {
    hostRemove(vnode.el);
  };

  /**
   * 将虚拟节点渲染到真实container上
   * @param vnode 虚拟dom
   * @param container 真实容器dom
   */
  const render = (vnode, container) => {
    if (vnode == null) {
      // 不是第一次渲染，vnode为null，表示将之前的虚拟节点删除
      if (container._vnode) {
        unmount(container._vnode);
      }
    }
    patch(container._vnode || null, vnode, container);
    container._vnode = vnode;
  };
  return {
    render,
  };
}
